const CustomStepper = (props: StepIconProps, label: string, index: number) =>
{
    const { active, completed, className } = props;

    return (
        <Grid container alignItems='center'>
            <Grid item style={{ marginRight: '8px' }}>
                <Grid container justifyContent='center' alignItems='center' style={{ width: '40px', height: '40px', borderRadius: 50, border: index < 3 ? '2px solid #5A91FF' : '2px solid #C4C4C4' }}>
                    <Grid container justifyContent='center' alignItems='center' style={{ width: '32px', height: '32px', borderRadius: 50, backgroundColor: index < 3 ? '#5A91FF' : '' }}>
                        <Typography style={{ fontFamily: 'Noto sans', color: index < 3 ? '#FFF' : '#C4C4C4', fontSize: '18px' }}>{`${ index + 1 }`}</Typography>
                    </Grid>
                </Grid>
            </Grid>
            <Grid item>
                <Typography style={{ fontFamily: 'Noto sans',  color: index < 3 ? '#5A91FF' :'#C4C4C4', fontSize: '18px' }}>{label}</Typography>
            </Grid>
        </Grid>
    )
}

